.popup {
    &__bg {
        width: 1024px;
        height: 768px;
        background: rgba(0, 0, 0, 0.6);
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        animation: fadeIn 0.5s ease-in-out 0.3s forwards;
        animation-iteration-count: 1;
    }
}

.slide-3 {

    &__container-3 {
        position: absolute;
        left: 150px;
        top: 140px;
        width: 860px;
        height: 564px;
    }

    &__content {
        margin-left: 260px;
        margin-top: 240px;
    }
}

.btn-close {
    position: absolute;
    right: 10px;
    top: 10px;
}

.navigation {
    position: absolute;
    bottom: 10px;
    left: 128px;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
